<script setup lang="ts" generic="TModel extends RadioValue, TValue extends TModel">
// TODO: Think about cancel value support
import { type InputHTMLAttributes, toRefs } from 'vue'
import { useVModel } from '@vueuse/core'

import { type ClassValue, cn } from '@/shared/lib/style'
import { useId } from '@/shared/lib/composables/useId'

export type RadioValue =
  | string
  | boolean
  | readonly unknown[]
  | Readonly<Record<string, unknown>>
  | number
  | null

export interface Props<
  TModel extends RadioValue, TValue extends TModel,
> extends /* @vue-ignore */ InputHTMLAttributes {
  id?: string
  class?: ClassValue
  rootClass?: ClassValue
  inline?: boolean
  leftLabel?: boolean
  modelValue?: TModel
  value?: TValue
}

export type Emits<TValue extends RadioValue> = {
  'update:modelValue': [value: TValue]
}

export interface Slots {
  default?: any
  description?: any
}

defineOptions({ inheritAttrs: false })

const props = defineProps<Props<TModel, TValue>>()

const emit = defineEmits<Emits<TValue>>()

const $slots = defineSlots<Slots>()

const { id, value } = toRefs(props)

const vModel = useVModel(props, 'modelValue', emit)

const computedId = useId(id)
</script>

<template>
  <div
    :class="cn(
      'flex items-start gap-3',
      leftLabel && 'flex-row-reverse',
      props.rootClass,
    )"
  >
    <input
      v-bind="$attrs"
      :id="computedId"
      v-model="vModel"
      :class="cn(
        `size-4 cursor-pointer border-gray-300 text-indigo-600
        focus:ring-indigo-600`,
        props.class,
      )"
      type="radio"
      :value="value"
    >

    <div
      v-if="$slots.default || $slots.description"
      :class="inline ? 'gap-1' : 'flex-col gap-2'"
      class="flex flex-wrap text-sm leading-4"
    >
      <label
        v-if="$slots.default"
        :for="computedId"
        class="font-medium text-gray-900"
      >
        <slot />
      </label>

      <p v-if="$slots.description" class="text-gray-500">
        <slot name="description" />
      </p>
    </div>
  </div>
</template>
